require('plug/selectordie/index');
require('plug/validate/validate');
let icheck = require('plug/icheck/index'),
    { payFrom, cancelUrl, subUrl, itemUrl } = require('api/Insurance'),
    { dateRange, countDown } = require('modules/date');
let insuranceList = {
    init() {
        let self = this;
        self.validate();
        self.payNow(); //
        self.cancel();
        $('.select').selectOrDie();
        icheck.init().checkAll();
        $(".timepicker").datetimepicker({
            format: 'yyyy-mm',
            weekStart: 1,
            autoclose: true,
            startView: 3,
            minView: 3,
            forceParse: false,
            language: 'zh-CN',
            /*  startDate: startDate,*/
            // endDate: new Date()
        })

        $('#timepicker-begin').on('changeDate', function(ev) {
            $('#timepicker-end').datetimepicker('setStartDate', ev.date);
        });
        $('#timepicker-end').on('changeDate', function(ev) {
            $('#timepicker-begin').datetimepicker('setEndDate', ev.date);
        });
        self.sub();
        self.timeOver();
    },
    validate() {
        let $form = $("#listForm");

        $("#submitBtn").click(function() {
            $form.submit();
        });

        $form.validate({
            rules: {
                personName: {
                    maxlength: 20,
                    letterCn: true
                },
                cardNum: {
                    isIdCard: true
                }
            },
            messages: {
                personName: {
                    maxlength: "1-20",
                    letterCn: "",
                },
                cardNum: {
                    isIdCard: ''
                }
            }
        })
    },
    payNow() {
        $('#J_btn-pay').click(function() {
            let dataJson = $('#payList').serialize();
            if (dataJson !== "") {
                payFrom(dataJson, ({ info, url }) => {
                    layer.alert(info, {
                        yes() {
                            if (url) {
                                window.location.href = url
                            }
                        }
                    });
                }, ({ info }) => {
                    if (info) {
                        layer.alert(info);
                    }
                })
            }
        });
    },
    //
    cancel() {
        $('.cancel_btn').click(function() {
            let $this = $(this),
                baseId = $this.data('baseid'),
                id = $this.data('id');
            layer.confirm("", {
                yes() {
                    cancelUrl({ baseId, id }, (data) => {
                        location.href = location.href;
                    }, ({ info }) => {
                        if (info) {
                            layer.alert(info);
                        }
                    })
                },
                cancel() {
                    layer.closeAll();
                }
            })

        })
    },
    //
    sub() {
        $('.sub_btn').click(function() {
            let $this = $(this),
                baseId = $this.data('baseid');

            itemUrl({ baseId: baseId }, (data) => {
                    let result = data.result,
                        socIsCancel = 0,
                        proIsCancel = 0;
                    if (result['1']) {
                        socIsCancel = 1
                    }
                    if (result['2']) {
                        proIsCancel = 1
                    }
                    layer.confirm("", {
                        yes() {
                            subUrl({ socIsCancel: socIsCancel, proIsCancel: proIsCancel, baseId: baseId }, (data) => {
                                location.href = location.href;
                            }, ({ info }) => {
                                if (info) {
                                    layer.alert(info);
                                }
                            })
                        },
                        cancel() {
                            layer.closeAll();
                        }
                    })
                }, ({ info }) => {
                    if (info) {
                        layer.alert(info);
                    }
                })
                /**/
        });
    },
    timeOver() {
        dateRange('create', 'pay');

        $('.pay-deadline').each(function() {
            let $this = $(this),
                value = $this.text().trim(),
                end = new Date(value),
                text = '',
                { timer } = $this.data();

            if (value == '/') {
                $this.html(`/`);

            } else {
                if (end.getTime() < new Date().getTime()) {
                    $this.html(``);

                } else {
                    timer = setInterval(function() {
                        let ret = countDown(end),
                            {
                                dd,
                                hh,
                                mm,
                                ss
                            } = ret;


                        if (!ret || end.getTime() < new Date().getTime()) {
                            clearInterval(timer);
                        }

                        $this.html(`${dd}${hh}${mm}${ss}`)
                    }, 1000)

                    $this.data('timer', timer);

                }

            }

        });
    }

}
module.exports = insuranceList;



